<template>
 <h2>Axios 포스트 방식 예제</h2>
</template>

<script>
//axios : 비동기 통신
import axios from "axios";

export default {
  data() {
    return {};
  },
  //비동기
  mounted() {
    axios.post("https://jsonplaceholder.typicode.com/posts",
    {
    userId:'1',
    title:'타이틀',
    body:'body 부분'
    }).then((response) => {
      console.log(response);
      console.log(response.data);
    });
  }
}
</script>

<style scoped></style>